.form{
	margin-top: 100px;
}

.btn{
	font-family: 'Courier New', Courier, monospace;
	font-size: 25px;
	font-weight: 800;
	padding: 10px;
	margin: 20px;
	color: black;
	background-color:wheat;
	cursor: pointer;
	transition: 0.5s;
	
}

.btn:hover{
	box-shadow: 8px 8px 4px brown;
	transition: 0.5s;
}

.pnotify-container{
display: inline-block;
